.searchItem{
    border: 1px solid lightgray;
    padding: 10px;
    border-radius: 5px;
    display: flex;
    justify-content: space-between;
    gap: 20px;
    margin-bottom: 20px;
}
.siImg{
    width: 200px;
    height: 200px;
    object-fit: cover;
}
.siDesc{
    display: flex;
    flex-direction: column;
    gap: 10px;
    flex: 2;
}
.siTitle{
    font-size: 20px;
    color: #0071c2;
}
.siDistance{
    font-size: 12px;
}
.siFeat1{
    font-size: 12px;
    background-color: #008009;
    color: white;
    width: max-content;
    padding: 3px;
    border-radius: 5px;
}
.siSubtitles{
    font-size: 12px;
    font-weight: bold;
}
.siFeat{
    font-size: 12px;
}

.siDetails{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.siDetailsTexts{
    text-align: right;
    display: flex;
    flex-direction: column;
    gap: 5px;
}
.siPrice{
    font-size: 24px;
}
.siInterval{
    font-size: 12px;
    color: gray;
}
.siCheck{
    background-color: #0071c2;
    color: white;
    font-weight: bold;
    padding: 10px 5px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}